import linkClickRouting from "../../mixins/linkClickRouting";

export default {
  name: 'Headercontact',

  mixins: [linkClickRouting],

  data() {
        return {
            contact: []
        }
    },
    async fetch() {
        const { json: data } = await this.$kirby.find({
            "query": "page('kontakt')",
            "select": {
                "title": true,
                "headertitle": true,
                "headertext": true,
                "headertram": true,
                "headeremail": true,
                "headertel": true,
                "headeropening": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.contact = data
    },
}
