export default {
    name: 'error',

    data() {
            return {
                error: []
            }
        },

  async fetch() {
        const { json: data } = await this.$kirby.find({
            "query": "page('error')",
            "select": {
                "headertext": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.error = data
    }, 
}
