export default {
    name: 'Footer',

    data() {
        return {
            footer: [],
            locations: []
        }
    },
    async fetch() {
        const { json: data } = await this.$kirby.find({
            "query": "page('kontakt')",
            "select": {
                "headeremail": true,
                "headertel": true,
                "headeropening": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.footer = data

        const { json: datalocations } = await this.$kirby.find({
            "query": "page('kontakt').children",
            "select": {
                "contenttext": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.locations = datalocations
    }
}
